@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=JetBrains+Mono:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  scroll-behavior: smooth;
}

:root {
  --blue: #81A1C1;
  --red: #BF616A;
  --orange: #D08770;
  --yellow: #EBCB8B;
  --green: #A3BE8C;
  --purple: #B48EAD;
  --bg-0: #f2f4f8;
  --bg-1: #ffffff;
  --ui-0: #434C5E;
  --ui-1: #4C566A;
  --ui-2: #7b88a1;
  --shadow-strong: rgba(184, 194, 215, 0.35);
  --shadow-weak: rgba(184, 194, 215, 0.3);
}

[data-theme=dark] {
  --bg-0: #242933;
  --bg-1: #2e3440;
  --ui-0: #eceff4;
  --ui-1: #b4b9c2;
  --ui-2: #abb9cf;
  --shadow-strong: rgba(15, 17, 21, 0.35);
  --shadow-weak: rgba(15, 17, 21, 0.15);
}

.fill--blue {
  fill: var(--blue);
}

.fill--red {
  fill: var(--red);
}

.fill--orange {
  fill: var(--orange);
}

.fill--yellow {
  fill: var(--yellow);
}

.fill--green {
  fill: var(--green);
}

.fill--purple {
  fill: var(--purple);
}

.fill--bg-0 {
  fill: var(--bg-0);
}

.fill--bg-1 {
  fill: var(--bg-1);
}

.fill--ui-0 {
  fill: var(--ui-0);
}

.fill--ui-1 {
  fill: var(--ui-1);
}

.fill--ui-2 {
  fill: var(--ui-2);
}

.fill--shadow-strong {
  fill: var(--shadow-strong);
}

.fill--shadow-weak {
  fill: var(--shadow-weak);
}

/* Move into layout at some point */
body {
  margin: 0;
  min-height: 100vh;
  background-color: var(--bg-0);
}

.landingpage {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding: 4em 2em;
}
.landingpage .title {
  max-width: 450px;
  color: var(--ui-0);
}
.landingpage .title h1 {
  font-size: 2.5em;
  font-weight: 700;
}
.landingpage .title h2 {
  font-size: 1.2em;
  opacity: 0.8;
}
.landingpage .github {
  padding: 1em;
  margin: 2em;
  background-color: var(--ui-0);
  opacity: 0.7;
}
.landingpage .github a {
  text-decoration: none;
  white-space: nowrap;
  color: var(--bg-1);
}
.landingpage .github:hover {
  transition: opacity 0.1s ease-in-out;
  opacity: 1;
  color: var(--ui-1);
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Rubik", sans-serif;
  margin-top: 0;
  margin-bottom: 0.5em;
  font-weight: normal;
}
h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
  text-decoration: none;
}

h1, h2 {
  font-weight: 600;
}

h3, h4 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.9rem;
}

h4 {
  font-size: 1.75rem;
}

h5 {
  font-size: 1.4rem;
}

h6 {
  font-size: 1.25rem;
}

p {
  line-height: 1.5em;
  margin-bottom: 2em;
}

p, a, b, span, button, ul, li, td, th {
  font-family: "Inter", sans-serif;
  font-size: 1rem;
}

code, code * {
  font-family: "JetBrains Mono", monospace;
  font-size: 0.9em;
}

p:not(:first-child) {
  margin-top: 0;
}
